import bulmaSteps from '../../../static/js/bulma-steps.min.js'
import utils from '~/common/utils'
import PostTweets from '~/components/PostTweets'
export default {
  middleware: 'authenticated',
  components: {
    PostTweets
  },
  async asyncData({ $axios, params }) {
    const [topic, nodes] = await Promise.all([
      $axios.get('/api/topic/edit/' + params.id),
      $axios.get('/api/topic/nodes')
    ])

    const extraData = JSON.parse(topic.extraData)
    let imageList = []
    if (!utils.isEmpty(topic.imageList)) {
      imageList = topic.imageList.split(',')
    }

    return {
      topic,
      nodes,
      postForm: {
        nodeId: topic.nodeId,
        title: topic.title,
        tags: topic.tags,
        content: topic.content,
        extraData,
        imageList
      }
    }
  },
  data() {
    return {
      publishing: false, // 当前是否正处于发布中...
      maxImageCount: 9,
      postForm: {
        nodeId: '',
        title: '',
        tags: [],
        content: '',
        extraData: {},
        imageList: []
      }
    }
  },
  computed: {
    currentUser() {
      return this.$store.state.user.current
    }
  },
  mounted() {
    console.log('imageList:' + this.postForm.imageList)
    bulmaSteps.attach('.steps', {
      beforeNext: (id) => {
        switch (id) {
          case 0:
            // must return an array of errors
            // if empty it goes to next step
            // if not empty it triggers onError with the first item of array
            return validateFirstStep()

          // second step
          case 1:
            return validateTwoStep()

          case 2:
            if (utils.isEmpty(this.postForm.title)) {
              this.$toast.error('请按照要求填满所有信息')
              return [{}]
            }
            break
        }
      },
      onError: (error) => {
        console.log(error)
      },
      onFinish: (msg) => {
        console.log(msg)
      }
    })

    const validateFirstStep = () => {
      const errors = []
      // if (validationRuleNotVerified) {
      if (
        utils.isEmpty(this.postForm.extraData.me_city) ||
        utils.isEmpty(this.postForm.extraData.me_age) ||
        utils.isEmpty(this.postForm.extraData.me_height) ||
        utils.isEmpty(this.postForm.extraData.me_gender) ||
        utils.isEmpty(this.postForm.extraData.me_edu) ||
        utils.isEmpty(this.postForm.extraData.me_work_city) ||
        utils.isEmpty(this.postForm.extraData.me_work_company) ||
        utils.isEmpty(this.postForm.extraData.me_hobby) ||
        utils.isEmpty(this.postForm.extraData.me_wechat) ||
        utils.isEmpty(this.postForm.extraData.me_other_info) ||
        this.postForm.imageList.length === 0
      ) {
        this.$toast.error('请按照要求填满所有信息及照片')
        errors.push('')
      }

      // }
      // if (otherValidationRuleNotVerified) {
      //   errors.push('Other validation rule not verified')
      // }
      return errors
    }

    const validateTwoStep = () => {
      const errors = []
      // if (validationRuleNotVerified) {
      if (
        utils.isEmpty(this.postForm.extraData.he_city) ||
        utils.isEmpty(this.postForm.extraData.he_age) ||
        utils.isEmpty(this.postForm.extraData.he_height) ||
        utils.isEmpty(this.postForm.extraData.he_edu) ||
        utils.isEmpty(this.postForm.extraData.he_work_city) ||
        utils.isEmpty(this.postForm.extraData.me_other_info)
      ) {
        this.$toast.error('请按照要求填满所有信息')
        errors.push('')
      }

      // }
      // if (otherValidationRuleNotVerified) {
      //   errors.push('Other validation rule not verified')
      // }
      return errors
    }
  },
  methods: {
    onImageUpload(imageList) {
      this.postForm.imageList = imageList
      // console.log('extraData:' + JSON.stringify(this.postForm.extraData))
    },
    configData() {
      return {
        title: this.postForm.title,
        me_city: this.postForm.extraData.me_city,
        me_age: this.postForm.extraData.me_age,
        me_height: this.postForm.extraData.me_height,
        me_wechat_show: this.postForm.extraData.me_wechat_show,
        me_photos_show: this.postForm.extraData.me_photos_show,
        me_gender: this.postForm.extraData.me_gender, // 0: boy; 1: girl
        me_edu: this.postForm.extraData.me_edu, // 学历
        me_work_city: this.postForm.extraData.me_work_city,
        me_work_company: this.postForm.extraData.me_work_company, // 工作单位
        me_hobby: this.postForm.extraData.me_hobby, // 兴趣爱好
        me_wechat: this.postForm.extraData.me_wechat, //
        me_other_info: this.postForm.extraData.me_other_info, // 择偶观
        me_photos: this.postForm.imageList.toString(), // 个人照片

        he_city: this.postForm.extraData.he_city,
        he_age: this.postForm.extraData.he_age, // 年龄要求
        he_height: this.postForm.extraData.he_height, // 身高
        he_gender: this.postForm.extraData.he_gender,
        he_edu: this.postForm.extraData.he_edu,
        he_work_city: this.postForm.extraData.he_work_city,
        he_other_info: this.postForm.extraData.he_other_info
      }
    },
    async submit() {
      const me = this
      if (me.publishing) {
        return
      }
      me.publishing = true

      try {
        const dd = JSON.stringify(this.configData())
        const topic = await this.$axios.post(
          '/api/topic/edit/' + this.topic.topicId,
          {
            nodeId: this.postForm.nodeId,
            title: this.postForm.title,
            content: this.postForm.content,
            tags: this.postForm.tags ? this.postForm.tags.join(',') : '',
            imageList: this.postForm.imageList.toString(),
            extraData: JSON.stringify(this.configData())
          }
        )
        this.$toast.success('提交成功', {
          duration: 1000,
          onComplete() {
            utils.linkTo('/love/' + topic.topicId)
          }
        })
      } catch (e) {
        console.error(e)
        me.publishing = false
        this.$toast.error('提交失败：' + (e.message || e))
      }
    }
  },
  head() {
    return {
      title: this.$siteTitle('修改话题')
    }
  }
}
